.root {
        height: 100vh,
    }
    .image {
        background-image: url(https://source.unsplash.com/random);
        background-repeat:  no-repeat;
        background-size: cover;
        background-position: center;
    }
    .paper {
        margin: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .avatar {
        margin: 1px;
        background-color: white;
    }
    .form {
        width: '100%';
        margin-top: 1px;
    }
    .submit {
        margin: 3px;
    }