nav{
    display: flex;
    justify-content: space-between;
    background-color: #1d809f;
    padding-right:20px;
    padding-left: 20px; 
    align-items: center;
}
.logo{
    font-size: 20px;
    color: #fff;
}
nav ul{
    list-style: none;
}
nav ul li{
    display: inline;
    margin-right: 20px;
}
nav ul li a{
    text-decoration: none;
    color: #fff;
}

